.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.cointenerHeaderMenu{
    background: #4ebb9d;
    display: flex;
    padding: 30px;
}
.buttonsHeader{
    margin: 20px;

}
.buttonHeaderMenu{
    width: 150px;
    margin: 0 10px 0 10px;
}
.userHeaderMenu{
    font-family: 'Neucha';
    font-size: 30px;
    color: beige;  
}
.txtWelcome{
    text-align: center;
    font-family: 'Neucha';
    font-size: 45px;
    color: #58524C;
    margin: 55px 0 0 0;
}
.txtIntro{
    text-align: center;
    font-family: 'Neucha';
    font-size: 35px;
    color: #58524C;
    margin: 0 0 30px 0;
}
.cointenerIntro{
    text-align: center;
    display: flex;
    margin-top: 25px;
}
.buttonsIntro{
    margin-top: 20px;
    width: 200px;
}
.wiatressBlock{
  padding: 45px 0 0 280px;
}
.waitressImg{
    max-width: 35%;
    text-align: center;
}
.chefBlock{
    padding: 45px 280px 0 0;
}
.chefImg{
    max-width: 35%;
}


.userNameTxt{
    font-family: 'Neucha';
    font-size: 35px;
    color: #58524C;
    margin-bottom: 10px;
}
.cointenerHome{
    text-align: center;
    margin: 0 auto;
}
.logo{
    margin: 55px 0 0 0;
    max-width: 15%;
    text-align: center;
}
.userName{
    font-family: 'Neucha';
    font-size: 25px;
    text-align: center;
    margin-top: 20px;
    width: 30%;
    padding: 20px;
    border-radius: 50px;
    border: solid #58524C 1px;
    background: #F5EAD6;
    color: #58524C;
}
.buttonsHome{
    margin-top: 20px;
    width: 200px;
}
.txtMenu{
    text-align: center;
    font-family: 'Neucha';
    font-size: 45px;
    color: #58524C;
    margin: 55px 0 0 0;
}
.cointenerMenu{
    text-align: center;
    display: flex;
    margin-top: 25px;
}
.txtMenu{
    font-family: 'Neucha';
    font-size: 35px;
    color: #58524C;
}
.buttonsMenu{
    margin-top: 20px;
    width: 200px;
}
.goBreakfastMenu{
    padding: 45px 0 0 280px;
}
.menuBreackfastImg{
    margin-top: 25px;
    max-width: 75%;
    text-align: center;
}
.goDinnerMenu{
    padding: 45px 280px 0 0;
}
.menuDinnerImg{
    max-width: 75%;
    text-align: center;
}

.cointenerButtonProduct{
    display: inline-block;
    background: beige;
    width: 200px;
    padding: 20px;
    margin: 10px;
    border-radius: 5%;
}
.imgButtonProduct{
    max-height: 80px;
}
.nameButtonProduct{
    font-family: 'Neucha';
    font-size: 25px;
    color: #58524C;
    margin-left: 10px;
}
.buttonPlusProduct{
    width: 50px;
    height: 50px;
    margin: 19px 20px 0 15px;
    background: #56cdb0;
    font-family: 'Neucha';
    font-size: 35px;
    color: #58524C;
    border-radius: 50%;
    border: none;
}
.cointenerComanda{
    font-family: 'Neucha';
    font-size: 45px;
    color: #58524c;
    background: beige;
    width: 800px;
    height: auto;
    text-align: center;
    padding: 50px 20px 20px 20px;
    border-radius: 5%;
    margin: 150px 50px 0 10px;
}
.numberTable{
    font-size: 30px;
    margin: 0;
}
.inputNumberTable{
    font-family: 'Neucha';
    font-size: 25px;
    text-align: center;
    margin-top: 5px;
    width: 300px;
    padding: 20px;
    border-radius: 50px;
    border: solid #58524C 1px;
    background: #F5EAD6;
    color: #58524C;
}
.listText{
    font-size: 25px;
    list-style:none;
}
.buttonDelete{
    width: 50px;
    height: 50px;
    margin: 19px 0 0 15px;
    background: #56cdb0;
    font-family: 'Neucha';
    font-size: 35px;
    color: #58524C;
    border-radius: 50%;
    border: none;
}
.total{
    font-size: 35px;
    border-top: #58524C 1px;
    text-align: center;
}
.buttonSendToKitchen{
    margin-top: 20px;
    width: 240px;
}
.txtDinnerMenu{
    text-align: center;
    font-family: 'Neucha';
    font-size: 45px;
    color: #58524C;
    margin: 55px 0 35px 0;
}
.cointenerComandaMenuDinner{
    text-align: center;
    height: auto;
    margin: 0 auto;
}
.buttonAndComanda{
    display: flex;
}
.cointenerKitchenCards{
    margin: 0 auto;
    margin-top: 30px;
    display: flex;
    
}
.cointenerCard{
    text-align: center;
    font-family: 'Neucha';
    font-size: 25px;
    width: 20%;
    background: beige;
    padding: 30px;
    margin: 20px;

}
.checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: #eee;
    border-radius: 50%;
  }
.listCard{
    list-style:none;
}
.buttonDone{
    width: 150px;
    margin: 10px;
}
